body {
  margin: unset;
  padding: unset;

  position: relative;
}

.join-content-bg {
  position: fixed;
  display: flex;
  flex-direction: row;
  
  gap: 1rem;

  min-width: fit-content;
  max-width: 80%;

  left: 50%;
  top: 10rem;
  transform: translateX(-50%);
}

.join-digit-plate {
  position: relative;
  display: block;

  background-color: rgb(0, 153, 255);
  height: 8rem;
  width: 5rem;
}

.join-digit-text {
  position: relative;
  display: block;
  width: 100%;

  text-align: center;

  margin: unset;
  font-size: 6rem;
}

.join-title {
  position: absolute;
  top: -7rem;
  left: 50%;
  transform: translateX(-50%);
  font-size: 3rem;
}

.join-page-loading-active {
  display: block !important;
}

.join-page-loading {
  position: absolute;
  display: none;
  height: 3rem;
  width: 3rem;
  left: 50%;
  transform: translateX(-50%);
  bottom: -5rem;
}

.spinner {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: auto;
  border: 4px solid transparent;
  border-top-color: #000000;
  border-radius: 50%;
  animation: loading-spinner 1s ease-in-out infinite;
}

@keyframes loading-spinner {
  to {
    transform: rotate(360deg);
  }
}

.hidden-input {
  opacity: 0;
}

.notifications-group {
  position: fixed;
  display: flex;
  flex-direction: column;

  right: 0;
  top: 0;

  gap: .7rem;

  width: 17rem;
  padding: 1.5rem;
}

.notification {
  background-color: rgba(255, 0, 0, .2);
  display: grid;
  position: relative;

  grid-template-rows: 2rem auto;

  min-height: 2rem;
  max-height: 5rem;
  padding: .5rem;
}

.notification::before {
  content: '';
  height: 100%;
  width: .3rem;
  position: absolute;
  background-color: rgb(255, 0, 0);
  left: -.3rem
}

.notification-close {
  position: absolute;
  top: .2rem;
  right: .2rem;

  height: 1.5rem;
  width: 1.5rem;

  font-size: 1.2rem;

  background: none;
  border: none;
  outline: none;
}

.notification-close:hover {
  color: rgb(82, 82, 82);
  cursor: pointer;
}

.notification-description, .notification-title {
  margin: unset;
}

.name-form {
  position: relative;
  top: 7rem;

  height: 20rem;
  width: 25rem;

  left: 50%;
  transform: translateX(-50%);

  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  text-align: center;
  gap: 2rem;
}

.name-submit {
  position: relative;
  left: 50%;
  transform: translateX(-50%);

  background-color: rgb(0, 153, 255);
  height: 3rem;
  width: 10rem;

  font-size: 1.7rem;
}

.name-input {
  position: relative;
  left: 50%;
  transform: translateX(-50%);

  height: 3rem;
  width: 20rem;
  font-size: 1.7rem;
}

.name-left {
  position: absolute;
  display: block;

  left: 2.1rem;
  transform: translateY(4.5rem);
}

.joined-bg {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  flex-direction: column;
}

.joined-button {
  height: 3rem;
  width: 15rem;
  background-color: rgb(113, 171, 204);
  border-radius: .4rem;
  border: .1rem solid black;
  font-size: 2rem;
}

.joined-button:hover {
  cursor: pointer;
  background-color: rgb(90, 180, 184);
}

.joined-title {
  font-size: 2rem;
}

.join-loading {
  height: 2rem;
  width: 2rem;

  position: relative !important;
}

.joined-place {
  color: rgb(76, 145, 235);
}

.create-page-btn {
  font-size: 2rem;
  position: fixed;
  display: block;

  right: 1rem;
  bottom: 1rem;
  background: none;
  border: none;

  transform: rotate(45deg);
}

.create-page-btn:hover {
  cursor: pointer;
  color: #3a3a3a;
}

.created-body {
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: center;
}

.created-body > h1 {
  text-align: center;
}

.list-methods {
  margin-top: 2rem;
  position: relative;
  width: 60vw;

  gap: 2rem;

  left: 50%;
  transform: translateX(-50%);

  justify-content: center;

  display: flex;
  flex-direction: row;
}

.digits-header {
  margin-top: 1rem;
  color: rgb(83, 186, 255);
  font-size: 8rem;
  margin-bottom: unset;
}

.created-url {
  font-size: 3rem;
  margin: .5rem;
}

.list-online {
  width: 100%;
  font-size: 2rem;
}

.list-online > h1 {
  text-align: center;
  margin: unset;
  font-size: 2rem; 
}

.list-manual {
  font-size: 2rem;
  width: 100%;
}

.list-manual > h1 {
  margin: unset;
  font-size: 2rem;
  text-align: center;
}

.manual-item {
  margin: .2rem;
  border-bottom: .1rem solid black;
}


.online-item {
  margin: .2rem;
  border-bottom: .1rem solid black;

  gap: 1rem;
  display: grid;
  grid-template-columns: 1rem auto 1fr 2rem;
}

.online-item > p {
  margin: unset;
}

.online-item:hover > button {
  display: block;
}

.online-item > button {
  padding-bottom: .25rem;
  height: 1.75rem;
  width: 1.75rem;
  display: none;
  background: none;
  border: none;
  font-size: 1rem;
}

.online-item > button:hover {
  border-radius: 1rem;
  background-color: rgb(201, 201, 201);
  cursor: pointer;
}

.item-time {
  font-size: 80%;
  color: darkslategray;
}

.item-num {
  color: rgb(28, 26, 39);
  font-weight: bold;
}

.del-room {
  position: fixed;
  width: 2rem;
  height: 2rem;
  font-size: 1.5rem;
  border: none;
  border-radius: 3rem;
  left: .5rem;
  top: .5rem;
}

.del-room:hover {
  cursor: pointer;
}